.nav .menu {
  display: table;
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
  list-style: none;
}

.nav .menu-item {
  display: table-cell;
  border-right: 1px solid #fff;
}

.nav .menu-item:first-child {
  border-left: 1px solid #fff;
}

.nav .menu-item-has-children {
  position: relative;
}

.nav .menu-item:hover .menu-link, .nav .menu-item-current .menu-link {
  background-color: #f56b3e;
  color: #fff;
}

.nav .menu-item:hover > .sub-menu {
  display: block;
}

.nav .menu-link {
  display: block;
  padding: 18px 5px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  text-align: center;
}

.nav .sub-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 9999;
  min-width: 220px;
  width: 100%;
  background-color: #384a6a;
}

.nav .sub-menu-item-has-children {
  position: relative;
}

.nav .sub-menu-item:hover > .sub-menu-link, .nav .sub-menu-item-current > .sub-menu-link {
  background-color: #f56b3e;
  color: #fff;
}

.nav .sub-menu-item:hover > .sub-menu {
  display: block;
}

.nav .sub-menu-link {
  display: block;
  padding: 10px 15px;
  color: #fff;
  text-decoration: none;
}

.nav .sub-menu .sub-menu {
  top: 0;
  left: 100%;
}

